<template>
    <div class="auth-container">
        <h2>Вход</h2>
        <form @submit.prevent="handleLogin">
        <div>
            <label for="username">Имя пользователя:</label>
            <input type="text" id="username" v-model="username" required />
        </div>
        <div>
            <label for="password">Пароль:</label>
            <input type="password" id="password" v-model="password" required />
        </div>
        <div>
            <button type="submit">Войти</button>
        </div>
        <p v-if="error" class="error">{{ error }}</p>
        </form>
        <p>Нет аккаунта? <router-link to="/register">Зарегистрироваться</router-link></p>
    </div>
</template>

<script>
import axios from 'axios';  // Подключаем axios

export default {
    data() {
        return {
        username: '',
        password: '',
        error: null,
        };
    },
    methods: {
        async handleLogin() {
        this.error = null;
        try {
            // Отправляем POST-запрос на сервер для логина
            const response = await axios.post('http://127.0.0.1:8000/api/login/', {
            username: this.username,
            password: this.password,
            });

            // Если все успешно, сохраняем токен и редиректим
            console.log('Logged in:', response.data);
            localStorage.setItem('access_token', response.data.access); // Сохраняем токен
            this.$router.push('/');  // Перенаправляем на домашнюю страницу
        } catch (error) {
            // Если ошибка, выводим сообщение
            this.error = error.response?.data?.detail || 'Неверные данные для входа';
        }
        },
    },
};
</script>

<style scoped>
.auth-container {
    max-width: 400px;
    margin: 0 auto;
    padding: 1rem;
    border: 1px solid #ccc;
    border-radius: 8px;
    background-color: #f9f9f9;
}

h2 {
    text-align: center;
}

input {
    width: 100%;
    padding: 8px;
    margin: 8px 0;
    border-radius: 4px;
    border: 1px solid #ccc;
}

button {
    width: 100%;
    padding: 10px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 4px;
}

button:hover {
    background-color: #45a049;
}

.error {
    color: red;
    font-size: 14px;
}
</style>
